<style scoped>
* {
  margin: 0;
  padding: 0;
}

.boss {
  width: 100%;
  z-index: 10;
}

ul {
  width: 100%;
  height: 36.5vh;
  overflow: auto;
}
.uls {
  width: 100%;
  height: 62vh;
  overflow: auto;
}

li {
  width: 55%;
  height: 30px !important;

  border: none;
  text-align: left;
  margin: 10px !important;
  line-height: 30px;
  padding: 0 5%;
}

.button {
  margin: 20px;
}

svg {
  width: 16px;
  height: 16px;
  float: left;
  margin-top: 6px;
}
.icon1 {
  position: relative;
  top: -3px;
  left: -5px;
}
li:hover {
  background: #b5b6b8 !important;
  border-radius: 6px;
}

.li {
  border-left: 4px solid #42b983 !important;
  background: #b5b6b8 !important;
  border-radius: 6px;
}
</style>
<template>
  <div class="boss">
    <ul :class="tmp ? '' : 'uls'">
      <li
        v-for="(item, index) in arr"
        :key="item.id"
        :style="
          key == item.id
            ? 'background: #b5b6b8 !important; border-radius: 7px!important;  border-left: 5px solid #409eff;'
            : ''
        "
        @click="btn(index, item.id)"
      >
        <svg
          data-v-a22cce3a=""
          xmlns="http://www.w3.org/2000/svg"
          xmlns:xlink="http://www.w3.org/1999/xlink"
          viewBox="0 0 24 24"
        >
          <g fill="none">
            <path
              d="M2.75 18h12.5a.75.75 0 0 1 .102 1.493l-.102.007H2.75a.75.75 0 0 1-.102-1.494L2.75 18h12.5h-12.5zm0-6.5h18.5a.75.75 0 0 1 .102 1.493L21.25 13H2.75a.75.75 0 0 1-.102-1.493l.102-.007h18.5h-18.5zm0-6.497h15.5a.75.75 0 0 1 .102 1.493l-.102.007H2.75a.75.75 0 0 1-.102-1.493l.102-.007h15.5h-15.5z"
              fill="currentColor"
            ></path>
          </g>
        </svg>
        <span v-if="count == index ? false : true" @dblclick="int(index)">
          {{ item.text }}</span
        >
        <input
          style="width: 80px"
          type="text"
          v-else
          v-model="html"
          @blur="handleBlur(index)"
        />
        <span style="float: right; margin-right: 10px" @click="off(index)">
          <img src="../assets/删除.png" alt=""
        /></span>
      </li>
    </ul>
    <div @click="addList" class="button">
      <div>
        <svg
          t="1692517026689"
          class="icon1"
          viewBox="0 0 1024 1024"
          version="1.1"
          xmlns="http://www.w3.org/2000/svg"
          p-id="14475"
          width="38"
          height="38"
        >
          <path
            d="M544.83 557.536l374.19 0.973c17.694 0.046 32.039-14.21 32.085-31.904 0.045-17.74-14.21-32.04-31.904-32.086l-374.258-0.995 0.566-372.561c0.022-17.627-14.256-31.995-31.95-32.041-17.672-0.022-32.018 14.278-32.064 31.973l-0.564 372.47-373.986-0.996c-17.695-0.045-32.04 14.21-32.086 31.904-0.022 8.849 3.552 16.903 9.368 22.719 5.769 5.77 13.734 9.345 22.537 9.367l374.076 0.996-0.565 375.955c-0.023 8.846 3.553 16.856 9.368 22.671 5.793 5.793 13.78 9.346 22.583 9.369 17.672 0.022 31.995-14.256 32.04-31.95L544.83 557.536 544.83 557.536z"
            fill="#707070"
            p-id="14476"
          ></path>
        </svg>
        <span>新建列表</span>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "LeftS",
  data() {
    return {
      state: true,
      count: null,
      key: null,
      arr: [],
      html: "",
      tmp: true,
    };
  },
  updated() {
    localStorage.setItem("arr11", JSON.stringify(this.arr));
    
  },

  methods: {
    off(index) {
      if (confirm("确定删除吗？")) {
        this.arr.splice(index, 1);
      }
    },
    handleBlur(index) {
      console.log(this.html);
      this.arr[index].text = this.html;
      this.count = null;
      this.$bus.$emit("int", this.html);
    },
    //方法
    int(value) {
      this.count = value;
      this.state = !this.state;
      this.html = this.arr[value].text;
      this.$bus.$emit("int", this.arr[value].text);
      console.log(this.html);
    },
    addList() {
      let arrList = {
        text: "待办事务",
        id: this.arr.length + 1,
        state: false,
        count: null,
      };
      this.arr.push(arrList);
    },
    btn(index, id) {
      this.$bus.$emit("int", this.arr[index].text);
      this.key = id;
    },
    data(data) {
      this.tmp = data;
    },
  },
  mounted() {
    this.$bus.$on("Data", this.data);
    if (localStorage.getItem("arr11") != null) {
      this.arr = JSON.parse(localStorage.getItem("arr11"));
    }
  },
  watch: {
    //监听
    op() {},
  },
  props: {
    msg: String,
  },
};
</script>